  width: 95%;
  margin: 20px auto 0;
}
#ListingPage .list-page-filter .rule-input {
  justify-content: center;
  gap: 14px;
  align-items: center;
  flex-wrap: wrap;
}
#ListingPage .list-page-filter .rule-input .rule-input-add-button {
  height: 56px;
}
#ListingPage .list-page-filter .rule-graph {
  overflow: auto;
}
#ListingPage .list-page-filter .rule-graph .rule-graph-list li,
#ListingPage .list-page-filter .rule-graph .rule-graph-list-nested li {
  margin-bottom: 4px;
}
#ListingPage .list-page-filter .rule-graph .rule-graph-list li > *,
#ListingPage .list-page-filter .rule-graph .rule-graph-list-nested li > * {
  flex: 0 0 auto;
}
#ListingPage .list-page-filter .rule-graph .rule-graph-list li::after,
#ListingPage .list-page-filter .rule-graph .rule-graph-list-nested li::after {
  content: "";
  flex: 1 1 auto;
}
#ListingPage .list-page-filter .rule-graph .rule-graph-list-nested {
  position: relative;
  margin-left: 16px;
}
#ListingPage .list-page-filter .rule-graph .rule-graph-list-nested::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 100%;
  border-top: 2px solid #777;
  border-bottom: 2px solid #777;
  border-left: 2px solid #777;
}
#ListingPage .list-page-filter .rule-graph .rule-graph-list-nested::after {
  content: "";
  position: absolute;
  top: 50%;
  left: -16px;
  width: 16px;
  border-top: 2px solid #777;
}
#ListingPage .list-page-toolbar {
  width: 95%;
  margin: 20px auto 0;
}
#ListingPage .list-page-table {
  width: 95%;
  overflow: hidden;
  margin: 20px auto 0;
}
#ListingPage .list-page-table .table-head-cell {
  font-weight: bold;
  background-color: #dedede;
}
#ListingPage .list-page-table .table-row-cell .content-row {
  margin: 0;
}
#ListingPage .list-page-table .table-row-cell .content-row .content-key {
  font-weight: bold;
}
