import React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import { CircularProgress, Stack, TextField } from '@mui/material';
import { green } from '@mui/material/colors';
import SnackbarAlert, { useAlert } from '../../../components/SnackbarAlert';

export default function UserCredentialDialogue(props: {
    title?: string
    username: string | null
    open: boolean
    hidePasswordInput?: boolean
    onClose: () => void
    onSubmit: (username: string, password: string) => Promise<void>
}) {
    const { title, username: pUsername, open, onClose, onSubmit } = props;
    const [submitting, setSubmitting] = React.useState(false);
    const [alertMsg, ] = useAlert();

    const [username, setUsername] = React.useState<string>('');
    const [password, setPassword] = React.useState<string>('');

    React.useEffect(() => {
        if (pUsername !== null) {
            setUsername(pUsername);
        } else {
            setUsername('')
        }

        setPassword('');
    }, [open, pUsername])


    const handleSubmit = React.useCallback(() => {
        setSubmitting(true);
        onSubmit(username, password).finally(() => {
            setSubmitting(false);
        })
    }, [username, password, onSubmit])

    return (
        <>
            <SnackbarAlert msg={alertMsg} />
            <Dialog
                open={open}
                onClose={(_, reason) => {
                    if (reason === 'escapeKeyDown')
                        onClose();
                }}
            >
                <DialogTitle>{title ?? ''}</DialogTitle>
                <DialogContent>
                    <Stack direction='column' sx={{
                        minWidth: '300px',
                        margin: '20px auto'
                    }}>
                        <TextField label=''
                            value={username}
                            onChange={ev => setUsername(ev.target.value)}
                            disabled={props.username !== null} />
                        {!props.hidePasswordInput &&
                            <TextField label=''
                                onChange={ev => setPassword(ev.target.value)}
                                onKeyDown={ev => {
                                    if (ev.key === 'Enter') {
                                        handleSubmit();
                                    }

                                }}
                                value={password} />
                        }
                    </Stack>
                </DialogContent>
                <DialogActions>
                    <Button onClick={onClose} disabled={submitting}></Button>
                    <Button onClick={handleSubmit} disabled={submitting}
                        variant='contained'
                    ></Button>
                    {submitting && <CircularProgress
                        size={24}
                        sx={{
                            color: green[500],
                            position: 'absolute',
                            top: '50%',
                            left: '50%',
                            marginTop: '-12px',
                            marginLeft: '-12px',
                        }}
                    />
                    }
                </DialogActions>
            </Dialog>
        </>
    );
}